.form {
    box-sizing: border-box;
        display: flex;
        flex-direction: column;
        width: 100%;
        padding: 30px 30px;
    
        border-radius: 10px;
        background-color:#eddff0;
        box-shadow: rgb(150, 150, 150) 10px 10px 20px;
    
        @media screen and (min-width: 480px) {
            width: 450px;
            margin: 0 auto;
            padding: 40px 50px;
        }
    
        @media screen and (min-width: 1280px) {
            width: 700px;
        }
}

.register__section {
    padding: 40px 15px;
}

.title {
    display: block;
    font-size: 18px;
    margin-bottom: 20px;

    @media screen and (max-width: 780px) {
    font-size: 14px;
    font-weight: 400;
    width:100%;
    }
}

.label {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
}

.input {
    font-size: 16px;
    border: none;
    background-color: #eddff0;
    border-bottom: 1px solid #000;
    width: 100%;
}

.register__button {
    border: none;
    padding: 8px 10px;
    border-radius: 10px;
    background-color: rgb(208, 0, 255);
    font-size: 16px;
    box-shadow: rgb(150, 150, 150) 5px 5px 10px;

    transition: all 250ms linear;

    &:hover {
        color: #fff;
        background-color: rgb(28, 144, 190);
    }

    &:disabled {
        opacity: 50%;
    }
}